// App.js
import React, { useState } from 'react';
import NowPlaying from './nowplaying';
import Calendar from './Calendar';
import bgImage from './assets/bg.png';
import './global.css';
import './Calendar.css';
import ToDoList from './ToDoList';
import RevisionCalendar from './RevisionCalendar';

function App() {
  const [searchQuery, setSearchQuery] = useState('');
  const [isSearchVisible, setSearchVisible] = useState(true);

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    if (!searchQuery.trim()) return;
    window.location.href = `https://www.google.com/search?q=${encodeURIComponent(searchQuery)}`;
  };

  const toggleSearchVisibility = () => {
    setSearchVisible(!isSearchVisible);
  };

  return (
    <div
      className="app-root"
      style={{
        background: `url(${bgImage}) no-repeat center center`,
        backgroundSize: 'cover',
      }}
    >
      <header className={`search-wrapper ${isSearchVisible ? 'search-visible' : 'search-hidden'}`}>
        {isSearchVisible && (
          <form className="search-form" onSubmit={handleSearchSubmit}>
            <input
              className="search-input"
              type="text"
              placeholder="Search Google"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              autoFocus
            />
          </form>
        )}
        <button
          className={`hide-search-button ${!isSearchVisible ? 'transformed' : ''}`}
          onClick={toggleSearchVisibility}
        >
          {isSearchVisible ? 'Hide' : 'Show'}
        </button>
      </header>

      <main className="widgets">
        <section className="widget">
          <NowPlaying />
        </section>

        <section className="widget">
          <Calendar />
        </section>

        <section className="widget">
          <ToDoList />
        </section>

        <section className="revision-calendar-wrapper">
          <RevisionCalendar />
        </section>
      </main>
    </div>
  );
}

export default App;