import React, { useEffect, useState } from 'react';
import ICAL from 'ical.js';
import './Calendar.css';

export default function Calendar() {
  const [events, setEvents] = useState([]);
  const [error, setError] = useState('');

  // 1 Fetch & parse the ICS file from the public folder
  useEffect(() => {
    const loadCalendar = async () => {
      try {
        // Assuming the ICS file is in the 'public' folder and accessible as /calendar.ics
        const res = await fetch('/calendar.ics');
        if (!res.ok) throw new Error(`HTTP ${res.status}`);
        const text = await res.text();

        // DEBUG: log the first bit so you can inspect it
        console.log('Raw ICS (first 500 chars):\n', text.slice(0, 500));

        const jcal = ICAL.parse(text);
        const comp = new ICAL.Component(jcal);
        const vevents = comp.getAllSubcomponents('vevent');
        console.log('Total VEVENTs found:', vevents.length);

        // 2 Parse each event, skipping malformed ones
        const parsed = vevents.reduce((acc, ve) => {
          try {
            const e = new ICAL.Event(ve);
            acc.push({
              summary: e.summary,
              location: e.location,
              start: e.startDate.toJSDate(),
              end: e.endDate.toJSDate(),
            });
          } catch (err) {
            console.warn('Skipping malformed VEVENT:', err.message);
          }
          return acc;
        }, []);

        if (parsed.length === 0) {
          throw new Error('No valid events found in calendar.');
        }

        // 3 Filter to next 6 upcoming events
        const upcoming = parsed
          .filter((e) => e.start > new Date())
          .sort((a, b) => a.start - b.start)
          .slice(0, 6);

        setEvents(upcoming);
      } catch (err) {
        console.error('Calendar load error:', err);
        setError(err.message);
      }
    };

    loadCalendar();
  }, []);

  if (error) {
    return <div className="calendar-error">Error loading calendar: {error}</div>;
  }

  return (
    <div>
      <h2 style={{ textAlign: 'center', color: '#fff', fontSize: '2rem' }}>
        Upcoming Events
      </h2>

      <ul className="calendar-list">
        {events.map((e, i) => (
          <li key={i} className="calendar-event">
            <span className="event-date">
              {e.start.toLocaleDateString()}{' '}
              {e.start.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}{' '}
              {' '}
              {e.end.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
            </span>
            <span className="event-summary">{e.summary}</span>
            {e.location && <span className="event-location">{e.location}</span>}
          </li>
        ))}
      </ul>
    </div>
  );
}
