import React, { useState } from 'react';

export default function RevisionCalendar() {
  // Compute this week's Monday
  const getMonday = date => {
    const d = new Date(date);
    const day = d.getDay();
    const diff = (day + 6) % 7; // days since Monday
    d.setDate(d.getDate() - diff);
    return new Date(d.getFullYear(), d.getMonth(), d.getDate());
  };

  const [startDate, setStartDate] = useState(() => getMonday(new Date()));
  const [events, setEvents] = useState({});
  const [showModal, setShowModal] = useState(false);
  const [modalDate, setModalDate] = useState(null);
  const [newEventText, setNewEventText] = useState('');
  const [newStartTime, setNewStartTime] = useState('09:00');
  const [newEndTime, setNewEndTime] = useState('10:00');

  // Build array of 7 days starting Monday
  const days = Array.from({ length: 7 }).map((_, i) => {
    const d = new Date(startDate);
    d.setDate(d.getDate() + i);
    return d;
  });

  const prevWeek = () => setStartDate(d => {
    const m = new Date(d);
    m.setDate(m.getDate() - 7);
    return m;
  });
  const nextWeek = () => setStartDate(d => {
    const m = new Date(d);
    m.setDate(m.getDate() + 7);
    return m;
  });

  const openModal = date => {
    setModalDate(date);
    setNewEventText('');
    setNewStartTime('09:00');
    setNewEndTime('10:00');
    setShowModal(true);
  };

  const saveEvent = () => {
    if (!newEventText.trim()) return;
    const key = modalDate.toISOString().split('T')[0];
    setEvents(prev => {
      const dayEv = prev[key] || [];
      return {
        ...prev,
        [key]: [...dayEv, { start: newStartTime, end: newEndTime, text: newEventText }]
      };
    });
    setShowModal(false);
  };

  const removeEvent = (dateKey, idx) => {
    setEvents(prev => {
      const dayEv = prev[dateKey] || [];
      const newDayEv = dayEv.filter((_, i) => i !== idx);
      const updated = { ...prev, [dateKey]: newDayEv };
      if (newDayEv.length === 0) delete updated[dateKey];
      return updated;
    });
  };

  return (
    <div className="revision-calendar-container">
      <div className="revision-calendar-header">
        <button onClick={prevWeek} className="nav-button"></button>
        <h2>
          Week of {startDate.toLocaleDateString('default', { month: 'short', day: 'numeric' })}
        </h2>
        <button onClick={nextWeek} className="nav-button"></button>
      </div>

      <div className="revision-week-grid">
        {days.map(day => {
          const key = day.toISOString().split('T')[0];
          const dayEvents = events[key] || [];
          return (
            <div key={key} className="revision-day-column">
              <div
                className="revision-day-header"
                onClick={() => openModal(day)}
              >
                <span className="revision-day-name">
                  {day.toLocaleDateString('default', { weekday: 'short' })}
                </span>
                <span className="revision-day-number">{day.getDate()}</span>
              </div>

              <ul className="revision-event-list">
                {dayEvents
                  .sort((a, b) => a.start.localeCompare(b.start))
                  .map((ev, i) => (
                    <li key={i} className="revision-event-item">
                      <span className="revision-event-time">{ev.start}{ev.end}</span>
                      <span className="revision-event-text">{ev.text}</span>
                      <button
                        className="nav-button"
                        style={{ padding: '0.25rem 0.5rem', fontSize: '0.75rem' }}
                        onClick={() => removeEvent(key, i)}
                      >
                        
                      </button>
                    </li>
                  ))}
              </ul>
            </div>
          );
        })}
      </div>

      {showModal && (
        <div className="revision-modal-overlay">
          <div className="revision-modal-content">
            <h3>New Event on {modalDate.toLocaleDateString()}</h3>
            <div className="revision-modal-time-pickers">
              <input
                type="time"
                value={newStartTime}
                onChange={e => setNewStartTime(e.target.value)}
                className="revision-modal-time"
              />
              <span>to</span>
              <input
                type="time"
                value={newEndTime}
                onChange={e => setNewEndTime(e.target.value)}
                className="revision-modal-time"
              />
            </div>
            <input
              type="text"
              placeholder="Event description..."
              value={newEventText}
              onChange={e => setNewEventText(e.target.value)}
              className="revision-modal-input"
            />
            <div className="revision-modal-actions">
              <button onClick={saveEvent} className="revision-modal-btn save">Save</button>
              <button onClick={() => setShowModal(false)} className="revision-modal-btn cancel">Cancel</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
